.poster {
   height: 552px;
   position: relative;
   flex: 0 20%;
   z-index: 1;
   overflow: hidden;
   cursor: pointer;
   opacity: 1;
   transition: 0.5s ease;
}

.poster:hover .poster-img {
   opacity: 0.3;
   z-index: 1;
}

.poster-data {
   width: 100%;
   height: 100%;
   padding: 20px;
   position: absolute;
   background-color: black;
   opacity: 1;
   color: white;
   transition: 0.5s ease;
}

.poster-title {
   font-size: 2rem;
   font-weight: 600;
   z-index: 100;
}

.poster-description {
   font-size: 1rem;
}

.backdrop {
   position: absolute;
}

.poster-img {
   width: 100%;
   height: 100%;
   z-index: -1;
   position: relative;
}
