/* Imports */
@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap");

/* Page Reset */
* {
   padding: 0;
   margin: 0;
   box-sizing: border-box;
   font-family: "Roboto Condensed", sans-serif;
}

/* Global Variables */
:root {
   --primary-color: #2980d9;
   --secondary-color: white;
}

/* HTML Tag Styling */

nav {
   width: 100%;
   height: 75px;
   background-color: var(--primary-color);
   box-shadow: 0px 5px 10px black;
   z-index: 100;
}

.navbar {
   height: 100%;
   width: 95%;
   margin: 0 auto;
   display: flex;
   justify-content: space-between;
   align-items: center;
}

.logo {
   font-size: 2rem;
   letter-spacing: 2px;
}

.logo a {
   color: white;
   text-decoration: none;
}

.navbar form {
   display: flex;
}

.navbar form input {
   color: black;
   margin-right: 10px;
}

form input:focus {
   outline: none;
}

/* Classes */

.container {
   margin: 0 auto;
   width: 90%;
}

.btn {
   padding: 10px 20px;
   font-size: 1rem;
   color: #7f8c8d;
   border: none;
   border-radius: 15px;
   text-transform: uppercase;
   background-color: var(--secondary-color);
}

.movie-search {
   width: 300px;
   padding: 10px;
   border-radius: 50px;
   border: none;
   color: white;
   background-color: var(--secondary-color);
}

.loading {
   text-align: center;
   margin-top: 300px;
   font-size: 3rem;
}

.posters {
   padding-bottom: 30px;
}

.movie-list {
   width: 100%;
   height: 100%;
   display: flex;
   flex-wrap: wrap;
   justify-content: space-between;
}

/* MoviePage CSS */

.movie-page {
   width: 100%;
   height: 100%;
   overflow: hidden;
}

.backdrop {
   position: absolute;
   background-image: cover;
   z-index: 1;
}

.backdrop-img {
   width: 100%;
   height: 100%;
}

.backdrop-overlay {
   position: relative;
   display: block;
   height: 100%;
   background: rgba(0, 0, 0, 0.6);
   z-index: 1;
   position: absolute;
   top: 0;
   left: 0;
   right: 0;
}

.movie-page-data {
   position: absolute;
   color: white;
   /* padding: 80px 0px 0px 80px; */
   top: 50%;
   transform: translateY(-50%);
   margin-left: 150px;
   z-index: 2;
   display: flex;
}

.movie-page-poster {
   width: 500px;
   box-shadow: 0px 0px 10px white;
}

.movie-page-content {
   margin-left: 30px;
   display: flex;
   flex-direction: column;
}

.movie-page-title {
   font-size: 3rem;
   letter-spacing: 2px;
}

.movie-page-overview {
   margin-top: 5px;
}
.movie-page-overview h2 {
   font-size: 1.8rem;
   letter-spacing: 1px;
   margin-bottom: 3px;
}

.movie-page-overview h4 {
   margin-top: 20px;
}

.movie-page-overview p {
   font-size: 1.1rem;
   width: 800px;
}

.back-arrow {
   color: white;
   position: absolute;
   z-index: 10;
   font-size: 1.4rem;
   margin-top: 40px;
   margin-left: 150px;
   text-decoration: none;
}

.back-arrow i {
   margin-right: 10px;
}

/* Media */

/* iPad Pro vertical */
@media only screen and (max-width: 1024px) {
   .movie-list {
   }

   .poster {
      flex: 1 33% !important;
      max-height: 513px;
   }

   .poster-data {
      display: none;
   }

   .logo {
      color: white;
   }
}

/* iPad vertical */
@media only screen and (max-width: 1024px) {
   .movie-list {
   }

   .poster {
      flex: 1 33% !important;
      max-height: 384px;
   }

   .poster-data {
      display: none;
   }

   .logo {
      color: white;
   }

   .searchForm {
      width: 300px;
   }
}

/* iPhone X */
@media only screen and (max-width: 375px) {
   .poster {
      flex: 1 50% !important;
      max-height: 281px;
   }

   .logo {
      color: white;
      font-size: 1rem;
   }

   .searchForm {
      width: 120px;
      margin-right: 30px;
   }
   .searchForm .btn {
      display: none;
   }
}
